@mixin on-tablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin on-desktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin content-padding-inline {
  padding-inline: 20px;

  @include on-tablet {
    padding-inline: 72px;
  }

  @include on-desktop {
    max-width: 1260px;
    margin-inline: auto;
    padding-inline: 120px;
  }
}

.container {
  @include content-padding-inline;
}

@mixin hover($property, $toValue) {
  transition: #{$property} $effect-duration;
  &:hover {
    #{$property}: $toValue;
  }
}

@mixin page-grid {
    --columns: 2;

    display: grid;
    column-gap: 20px;
    grid-template-columns: repeat(var(--columns), 1fr);

    @include on-tablet {
      --columns: 6;

      column-gap: 24px;
    }

    @include on-desktop {
      --columns: 12;
    }
}

@mixin image-hover($selector: 'img') {
  #{$selector}:hover {
    cursor: pointer;
    transform: scale(1.1);
    transition: transform 0.2s;
    z-index: 2;
  }
}
