.contact {
  @include page-grid;

  &__titles {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: span 6;
    }

    @include on-desktop {
      grid-column: span 5;
    }
  }

  &__contents {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: span 6;
    }

    @include on-desktop {
      grid-column: 7 / -1;
    }
  }
}

input[type="text"] {
  width: 100%;
  padding: 12px;
  border: 1px solid #fafafa;
  background-color: #F8F8FA;
  border-radius: 8px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
  height: 48px;
  outline: none;
  box-shadow: 1px 1000px #F8F8FA inset;
}

input[type="email"] {
  width: 100%;
  padding: 12px;
  border: 1px solid #fafafa;
  background-color: #F8F8FA;
  border-radius: 8px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
  height: 48px;
  outline: none;
  box-shadow: 1px 1000px #F8F8FA inset;
}

textarea {
  width: 100%;
  padding: 14px 16px;
  border: 1px solid #fafafa;
  background-color: #F8F8FA;
  border-radius: 8px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: none;
  height: 148px;
}

input[type="submit"] {
  display: block;
  font-weight: 700;
  width: 100%;
  height: 56px;
  margin-top: 20px;
  margin-bottom: 40px;
  padding: 0 45%;
  border-radius: 8px;
  border: 2px solid #F0F1F4;
  background-color: #F8F8FA;
  cursor: pointer;
  transition: background-color 0.3s,
  color 0.3s, border-color 0.3s;
}

input[type="submit"]:hover {
  background-color: #111;
  color: #FFF;

}


.contact__info {
  margin: 0;
  padding: 0;


  h3 {
    font-family: 'Space Grotesk', sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-align: left;
    color: #9393A3;
  }

  p {
    font-family: 'Space Mono', sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    margin-bottom: 40px;
    letter-spacing: 0;
    margin-top: -10px;
  }

  a {
    color: black;
    text-decoration: none;
    &:hover {
      text-decoration: underline;
    }
  }
  address {
    font-style: normal;
  }
}
